.contact-wrapper {
  font-size: 2rem;
  background-color: #fff;
  color: #333;
  padding: 10rem 0;

  display: flex;
  align-items: flex-start;
  justify-content: flex-start;

  @media only screen and (max-width: 84.0625em) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media only screen and (max-width: 25em) {
    padding: 5rem 0;
  }
}
